import React from 'react';
import BlogCard from './BlogCard';

// BlogCategorySection Component
export default function BlogCategorySection({ category, posts }) {
  // If there are no posts in this category, don't render the section
  if (!posts || posts.length === 0) {
    return null;
  }

  return (
    <div className="mb-16">
      <h2 className="text-2xl font-bold mb-6 pb-2 border-b">{category}</h2>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {posts.map(post => (
          <BlogCard key={post.slug} post={post} />
        ))}
      </div>
    </div>
  );
}