import React from 'react';

// BlogFilters Component
export default function BlogFilters({ categories, activeCategory, onSelectCategory }) {
  return (
    <div className="flex flex-wrap items-center justify-start gap-4 mb-8 overflow-x-auto pb-2">
      <span className="font-medium text-gray-700">Filter by:</span>
      <button 
        className={`blog-filter-button ${activeCategory === 'all' ? 'active' : ''}`}
        onClick={() => onSelectCategory('all')}
      >
        All Posts
      </button>
      
      {categories.map(category => (
        <button 
          key={category} 
          className={`blog-filter-button ${activeCategory === category ? 'active' : ''}`}
          onClick={() => onSelectCategory(category)}
        >
          {category}
        </button>
      ))}
    </div>
  );
}
